.subscription-card {
  box-sizing: border-box;
  width: 100%;
  aspect-ratio: 360/448;
  padding: 55px 35px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 7px solid transparent;
  background-color: $color-default-white;

  @include hover-focus {
    border-image-source: $border-linear;
    border-image-slice: 1;
  }

  &:active {
    border-image-source: $border-linear;
    border-image-slice: 1;
  }

  @include vp-1199 {
    aspect-ratio: 442/448;
  }

  @include vp-767 {
    min-width: 290px;
    aspect-ratio: 290/384;
    padding: 44px 35px;
  }
}

.subscription__order-link {
  width: 206px;
  padding-top: 23px;
  padding-bottom: 23px;

  @include vp-767 {
    padding-top: 24px;
    padding-bottom: 24px;
  }
}

.subscription-card h4,
.subscription-card__header {
  margin: 0;
  padding-bottom: 3px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 28px;
  color: $sapphire;

  @include vp-767 {
    font-size: 22px;
    line-height: 24px;
    padding-bottom: 1px;
  }
}

.subscription-card__property {
  margin: 0;
  padding-bottom: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 40px;
  color: $chambray;
}

.subscription-card__price-container {
  position: relative;
  padding-top: 52px;
  padding-bottom: 35px;
  z-index: 1;
  width: 100%;
  display: flex;
  justify-content: center;

  @include vp-767 {
    padding-top: 41px;
    padding-bottom: 30px;
  }
}

.subscription-card__price {
  margin: 0;
  background-image: url("../img/svg/rouble.svg");
  padding-right: 44px;
  background-position: bottom 24px right 0;
  background-size: 30px 42px;
  background-repeat: no-repeat;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
  color: $egyptian-blue;
  word-break: break-word;

  @include vp-767 {
    background-size: 24px 33px;
    background-position: bottom 19px right 0;
    font-size: 48px;
    line-height: 72px;
    padding-right: 34px;
    padding-left: 4px;
  }
}

.subscription-card__price-shadow {
  position: absolute;
  width: 100%;
  z-index: -1;
  top: 30px;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 100px;
  line-height: 85px;
  color: $whisper;
  opacity: 0.48;
  text-align: center;

  word-break: break-word;

  @include vp-767 {
    top: -3px;
    font-size: 80px;
    line-height: 120px;
  }
}
