.main-header {
  color: $color-default-white;
  background: $desktop-conic;
  min-width: 320px;
}

.main-header__container {
  position: relative;
  box-sizing: border-box;
  padding-top: 30px;
  min-height: 658px;

  @include vp-1199 {
    position: static;
    min-height: 554px;
    padding-top: 36px;
  }

  @include vp-767 {
    min-height: 491px;
    padding-top: 15px;
  }
}

.main-header__menu-wrapper {
  position: absolute;
  width: 1160px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;

  @include vp-1199 {
    position: static;
    width: 100%;
  }
}

.main-header__logo-link {
  @include vp-1199 {
    padding-top: 5px;
  }

  @include vp-767 {
    padding-top: 0;
  }
}

.main-header__logo-link img {
  width: 121px;
  height: 44px;

  @include vp-1199 {
    width: 82px;
    height: 17px;
  }

  @include vp-767 {
    width: 69px;
    height: 24px;
  }
}

.main-header__contacts-link {
  grid-column: 2;
  justify-self: flex-end;
  width: 170px;

  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  text-align: right;
  color: $color-default-white;

  @include vp-1199 {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }

  @include vp-767 {
    padding-top: 2px;
    font-size: 20px;
    line-height: 24px;
  }
}

.main-header__intro {
  padding-top: 21px;

  @include vp-1199 {
    padding-top: 17px;
    padding-bottom: 20px;
  }
}
