.main-footer {
  min-width: 320px;
  background-color: $black-russian;
}

.main-footer__container {
  display: grid;
  min-height: 108px;
  padding-top: 10px;
  grid-template-columns: 88px 1fr minmax(100px, max-content);
  gap: 40px;

  @include vp-1199 {
    padding-top: 4px;
    grid-template-columns: 82px 1fr minmax(100px, max-content);
    gap: 25px;
  }

  @include vp-767 {
    position: relative;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding-top: 48px;
    padding-bottom: 52px;
    gap: 36px;
  }
}

.main-footer__logo-link {
  display: flex;
  align-items: center;

  @include vp-767 {
    position: absolute;
    margin-top: 2px;
  }
}

.main-footer__logo-link img {
  width: 88px;
  height: 32px;

  @include vp-1199 {
    width: 82px;
    height: 18px;
  }

  @include vp-767 {
    width: 68px;
    height: 15px;
  }
}

.main-footer__navigation {
  justify-self: center;
}
